/* Keyframes */
@-webkit-keyframes star {
  0% {
    opacity: 0;
    -webkit-transform: scale(1.5) translateY(-0.75em);
            transform: scale(1.5) translateY(-0.75em); }
  20% {
    opacity: 1; }
  89% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1); }
  100% {
    opacity: 1; } }
@keyframes star {
  0% {
    opacity: 0;
    -webkit-transform: scale(1.5) translateY(-0.75em);
            transform: scale(1.5) translateY(-0.75em); }
  20% {
    opacity: 1; }
  89% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1); }
  100% {
    opacity: 1; } }

@-webkit-keyframes wars {
  0% {
    opacity: 0;
    -webkit-transform: scale(1.5) translateY(0.5em);
            transform: scale(1.5) translateY(0.5em); }
  20% {
    opacity: 1; }
  90% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1); }
  100% {
    opacity: 1; } }

@keyframes wars {
  0% {
    opacity: 0;
    -webkit-transform: scale(1.5) translateY(0.5em);
            transform: scale(1.5) translateY(0.5em); }
  20% {
    opacity: 1; }
  90% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1); }
  100% {
    opacity: 1; } }

@-webkit-keyframes spin-letters {
  0%, 10% {
    opacity: 0;
    -webkit-transform: rotateY(90deg);
            transform: rotateY(90deg); }
  30% {
    opacity: 1; }
  70%, 86% {
    -webkit-transform: rotateY(0);
            transform: rotateY(0);
    opacity: 1; }
  95%, 100% {
    opacity: 1; } }

@keyframes spin-letters {
  0%, 10% {
    opacity: 0;
    -webkit-transform: rotateY(90deg);
            transform: rotateY(90deg); }
  30% {
    opacity: 1; }
  70%, 86% {
    -webkit-transform: rotateY(0);
            transform: rotateY(0);
    opacity: 1; }
  95%, 100% {
    opacity: 1; } }

@-webkit-keyframes move-byline {
  0% {
    -webkit-transform: translateZ(5em);
            transform: translateZ(5em); }
  100% {
    -webkit-transform: translateZ(0);
            transform: translateZ(0); } }

@keyframes move-byline {
  0% {
    -webkit-transform: translateZ(5em);
            transform: translateZ(5em); }
  100% {
    -webkit-transform: translateZ(0);
            transform: translateZ(0); } }

/* Animation properties */
.starwars-logo {
  -webkit-perspective: 500px;
          perspective: 500px;
  -webkit-transform-style: preserve3d;
          transform-style: preserve3d;
  height: 17em;
  left: 50%;
  position: absolute;
  top: 35%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 34em; }
  .starwars-logo .star {
    position: absolute;
    width: 100%;
    top: -0.75em;
    -webkit-animation: star 5s ease-out;
            animation: star 5s ease-out; }
  .starwars-logo .wars {
    position: absolute;
    width: 100%;
    bottom: -0.5em;
    -webkit-animation: wars 5s ease-out;
            animation: wars 5s ease-out; }
  .starwars-logo .byline {
    position: absolute;
    color: #fff;
    font-family: "ITC Serif Gothic", Lato;
    font-size: 2.25em;
    left: -2em;
    letter-spacing: 0.4em;
    right: -2em;
    text-align: center;
    text-transform: uppercase;
    top: 41%;
    -webkit-animation: move-bylin 5s linear 1;
            animation: move-bylin 5s linear 1; }
    .starwars-logo .byline span {
      display: inline-block;
      -webkit-animation: spin-letters 5s linear 1;
              animation: spin-letters 5s linear 1; }

/*** Media queries for adjusting to different screen sizes ***/
@media only screen and (max-width: 600px) {
  .starwars-logo {
    font-size: 10px; } }

@media only screen and (max-width: 480px) {
  .starwars-logo {
    font-size: 7px; } }

